:root {
    --bs-border-color: var(--tc-system-stroke) !important;
    --bs-primary-rgb: 236, 171, 35;
    --bs-border-radius-xl: 10px;
    --bs-border-radius-xxl: 20px;
    --bs-dark-rgb: 44, 46, 60;

    .bg-dark-3 {
        background-color: #2d2d2d;
    }
}

// background colors

.bg-light {
    background-color: var(--tc-bg-light) !important;
}
.bg-dark-2 {
    background: var(--tc-system-black-2);
}

// text colors

.text-yellow {
    color: var(--tc-system-yellow) !important;
}
.text-secondary-2 {
    color: #afafaf;
}
.btn-text {
    font-family: var(--tc-btn-font-family);
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 100%;
    /* 14px */
    text-transform: uppercase;
}
.fill-primary {
    path {
        fill: var(--tc-theme-primary) !important;
    }

    fill: var(--tc-theme-primary) !important;
}

.fill-white {
    path {
        fill: var(--tc-system-white) !important;
    }

    fill: var(--tc-system-white) !important;
}

.filter-invert {
    filter: invert(1);
}

.contact-map {
    max-height: 700px;

    iframe {
        height: 700px;
        width: 100%;
    }
}

.map {
    -webkit-filter: grayscale(75%);
    -moz-filter: grayscale(75%);
    -ms-filter: grayscale(75%);
    -o-filter: grayscale(75%);
    filter: grayscale(75%);
}
